import React, { Component } from "react";
import {Link } from "react-router-dom";
import CardDeck from 'react-bootstrap/CardDeck';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import {connect} from 'react-redux';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faYoutube,
    faFacebook,
    faTwitter,
    faInstagram
  } from "@fortawesome/free-brands-svg-icons";
import Pagination from '../pagination';
import {loadInfluencers, paginate, SearchQuery} from '../../actions/actionfile';
import {withRouter} from 'react-router-dom';





class CompanyDashboard extends Component{
    constructor(props) {
        super(props);
        this.state = {
            influencers: [],
            filteredInfl: [],
            pageCount: 5,
            perPage: 2,
            currentPage: 0,

        };
        this.handleChange = this.handleChange.bind(this);
        this.onClickHire = this.onClickHire.bind(this);

    }

    componentDidMount(){
        console.log("Going to call loadInfluecner action")
        this.props.dispatch(loadInfluencers())
        
    }

    paginationClick(pageNum){
        this.props.dispatch(paginate(pageNum))
 
    }
    onSearch(){
        this.props.dispatch(SearchQuery(this.state.searchQuery))
    }
    handleChange = (e) => {
        this.setState({ [e.target.name]:e.target.value });
    }

    onClickHire(e, name, image){
        e.preventDefault()
        localStorage.setItem('name', name);
        localStorage.setItem('imurl', image);
        this.props.history.push("/hireInflu")
    }
    

    render(){
        console.log("Influencers",this.props.influencers)
        return(
            <div className="container" style={{"marginTop": "3rem"}}>
                <h1>Influencers </h1>
                <Form inline>
                    <FormControl type="text" name="searchQuery" placeholder="Search by city/niche" className="mr-sm-2" onChange = { (e) => this.handleChange(e)}>
                       </FormControl>
                    <Button variant="outline-success" onClick={ this.onSearch.bind(this)}>Search</Button>
                </Form><br/>

                
                   <CardDeck>
                    {
                        this.props.influencers && this.props.influencers.map( influencer =>(
                            
                                <Card key={influencer.id} style={{ width: '18rem' }}>
                                <Card.Img variant="top" src={influencer.image} />
                                <Card.Body>
                                <Card.Title>{influencer.name}</Card.Title>
                                <Card.Text>
                                {influencer.description}
                                </Card.Text>
                                <p >
                                    <a href={influencer.youtube_link}
                                    className="youtube social">
                                    <FontAwesomeIcon icon={faYoutube} size="2x" style={{color:"red"}}/>
                                    </a>
                                    <a href="https://www.facebook.com/learnbuildteach/"
                                    className="facebook social">
                                    <FontAwesomeIcon icon={faFacebook} size="2x" style={{color:"blue"}}/>
                                    </a>
                                    <a href="https://www.twitter.com/jamesqquick" className="twitter social">
                                    <FontAwesomeIcon icon={faTwitter} size="2x" style={{color:"skyblue"}}/>
                                    </a>
                                    <a href="https://www.instagram.com/learnbuildteach"
                                    className="instagram social">
                                    <FontAwesomeIcon icon={faInstagram} size="2x" style={{color:"#8a3ab9"}}/>
                                    </a>
                                </p>
                                {/* <Link to="/hireInflu">
                                
                                </Link> */}
                                <Button variant="success" onClick={e => this.onClickHire(e,influencer.name, influencer.image)}>Hire Now</Button>
                                </Card.Body>
                            
                                </Card>
                            
                        ))
                    }
                   </CardDeck>
                <Pagination currentPage={this.props.currPage} perPage={this.props.perPage} pageCount={this.props.pageCount} paginationClick={(a,b) => this.paginationClick(a,b)}/>
            </div>

        )
    }
}

function mapStateToProps(state){
    console.log("==== MAp State========>",state)
    return {influencers : state.search.influencers, search: state.search.search, currPage: state.search.currPage, perPage: state.search.perPage, pageCount: state.search.pageCount}
}



export default  withRouter(connect(mapStateToProps)(CompanyDashboard));