import React from 'react';
import { Component } from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import {updateInfluencerDetails, profile} from '../../actions/actionfile';
import {connect} from 'react-redux';

const uurl = 'http://localhost:5000/influencers/';

class updateForm extends Component{
    constructor(props){
        super(props)
        console.log("props", props)
        this.profile = React.createRef(props.influencersProfile);
        this.state = {
            
        }
        this.handleChange = this.handleChange.bind(this);
    }

    updateProfile = (e) => {
        e.preventDefault()
        console.log("====Update Profile ==",this.state)
        this.props.dispatch(updateInfluencerDetails(this.state.profile))
        // this.setState({...this.props.influencersProfile})
        alert("Details successfuly updated!")
    }
    getInitialState(){
        console.log("Get Initial state called")
        return {profile:this.props.influencersProfile}
    }
    componentDidMount(){
        // loggedin influencer details
        this.props.dispatch(profile(1))
        console.log("========Component Did Mount==========>",this.props.influencersProfile)
        // this.setState({...this.props.influencersProfile})
    }

    componentDidUpdate(prevProps){
        if(this.props.influencersProfile != prevProps.influencersProfile){
            this.setState({profile: this.props.influencersProfile})
        }
    }
    
    handleChange(e) {
        // console.log("==============niche,city",e.target.name,e.target.value)
        const newChange ={
            ...this.state.profile,
            [e.target.name] : e.target.value
        }
        this.setState({ profile:newChange });
     }

    //  static getDerivedStateFromProps(props, state) {
    //     return {...props.influencersProfile };
    //   }

    render(){
        
        return(
            <div className="container" style={{marginTop: "5rem"}}>
                <Form>
                    <Form.Group controlId="formBasicNiche">
                        <Form.Label>Niche</Form.Label>
                        <Form.Control type="text" name="niche" placeholder="Enter niche" value={this.state.profile && this.state.profile.niche} onChange={this.handleChange}/>
                    </Form.Group>

                    <Form.Group controlId="formBasicCity">
                        <Form.Label>City</Form.Label>
                        <Form.Control type="text" name="city" value={this.state.profile && this.state.profile.city} placeholder="Enter Your city" onChange={this.handleChange}/>
                    </Form.Group>
                    
                    <Tabs defaultActiveKey="profile" id="uncontrolled-tab-example">
                        <Tab eventKey="home" title="Facebook">
                            <p>Facebook</p>
                                <Form.Group as={Row} controlId="formPlaintextUrl">
                                    <Form.Label column sm="2">
                                    URL
                                    </Form.Label>
                                    <Col sm="10">
                                    <Form.Control type="text" name="fbURL" value={this.state.profile && this.state.profile.fbURL} placeholder="url" onChange={this.handleChange} />
                                    </Col>
                                </Form.Group>

                                <Form.Group as={Row} controlId="formPlaintextLives">
                                    <Form.Label column sm="2">
                                        Like
                                    </Form.Label>
                                    <Col sm="10">
                                    <Form.Control type="text" name="fbLikes" placeholder="likes" value={this.state.profile && this.state.profile.fbLikes} onChange={this.handleChange} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} controlId="formPlaintextSubscribe">
                                    <Form.Label column sm="2">
                                        Follower
                                    </Form.Label>
                                    <Col sm="10">
                                    <Form.Control type="text" name="fbFollow" placeholder="follower" value={this.state.profile && this.state.profile.fbFollow} onChange={this.handleChange}/>
                                    </Col>
                                </Form.Group>
                        </Tab>
                    <Tab eventKey="profile" title="Instagram">
                        <p>Instagram</p>
                    <Form.Group as={Row} controlId="formPlaintextUrl">
                        <Form.Label column sm="2">
                        URL
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control type="text" name="instUrl" value={this.state.profile && this.state.profile.instUrl} placeholder="url" onChange={this.handleChange}/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formPlaintextLikes">
                        <Form.Label column sm="2">
                            Like
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control type="text" name="instLike" placeholder="likes" value={this.state.profile && this.state.profile.instLike} placeholder="likes" onChange={this.handleChange}/>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formPlaintextSubscribe">
                        <Form.Label column sm="2">
                            Follower
                        </Form.Label>
                        <Col sm="10">
                        <Form.Control type="text" name="instFollow" placeholder="follower" value={this.state.profile && this.state.profile.instFollow} onChange={this.handleChange}/>
                        </Col>
                    </Form.Group>
                    </Tab>
                    <Tab eventKey="contact" title="YouTube" >
                        <p>Youtube</p>
                        <Form.Group as={Row} controlId="formPlaintextUrl">
                            <Form.Label column sm="2">
                            URL
                            </Form.Label>
                            <Col sm="10">
                            <Form.Control type="text" name="utubeUrl" placeholder="url" value={this.state.profile && this.state.profile.utubeUrl} onChange={this.handleChange}/>
                            </Col>
                        </Form.Group>

                        <Form.Group as={Row} controlId="formPlaintextLives">
                            <Form.Label column sm="2">
                                Like
                            </Form.Label>
                            <Col sm="10">
                            <Form.Control type="text" name="utubeLike" placeholder="likes" value={this.state.profile && this.state.profile.utubeLike} onChange={this.handleChange} />
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row} controlId="formPlaintextSubscribe">
                            <Form.Label column sm="2">
                                Subscriber
                            </Form.Label>
                            <Col sm="10">
                            <Form.Control type="text" name="utubeSubscribers" placeholder="follower" value={this.state.profile && this.state.profile.utubeSubscribers} onChange={this.handleChange}/>
                            </Col>
                        </Form.Group>
                    </Tab>
                </Tabs><br/>
                <Button variant="success" type="submit" style={{float:"right"}} onClick={this.updateProfile}>
                        Update
                    </Button>


                

                
                    
                </Form>
            </div>


        )
    }
}

function mapStateToProps(state){
    console.log("==== MAp State========>",state)
    return {influencersProfile : state.search.influencersProfile}
}

export default connect(mapStateToProps)(updateForm);