const url = "http://localhost:5000/influencers";
const Hurl = "http://localhost:5000/jobs";

export function SearchQuery(searchQuery){
    const output = fetch(`${url}?q=${searchQuery}`,{method:'GET'})
    .then((data) =>  data.json())

    return {
        type:'SEARCH_QUERY',
        payload:output
    }
}


export function loadInfluencers(){

    const output = fetch(url,{method:'GET'}).then(data=>data.json())
    console.log("API action called", output)
    return {
        type:'INFLUENCER_ALL',
        payload:output
    }

    
}

export function paginate(pageNum){
    console.log("================> Acion creator", pageNum)
    return {
        type:'INFLUENCER_PAGINATE',
        payload:{
            pageNum: pageNum
        }
    }
}

export function profile(uid){
    console.log("Feching logged in user profile", uid)
    const output = fetch(`${url}/${uid}`,{method:'GET'})
    .then((data) =>  data.json())

    return {
        type:'FETCH_PROFILE',
        payload:output
    }
}

export function updateInfluencerDetails(profile){
    console.log("================> Acion creator", profile)
    const output = fetch(`${url}/${profile.id}`, {method: 'PUT',headers: { 'Content-Type': 'application/json'},
    body: JSON.stringify({ ...profile })})
    .then((data) => {
        
        // console.log("========data",data)
        data.json()
    })
    
    .catch(err => {
        console.log(err);
        alert("Profile update failed, please try again!");
      })
    return {
        type: 'UPDATE_INFLUENCERS_PROFILE',
        payload: {...profile},
        err: []
    }
    
}

