    const output = fetch(Hurl, {method: 'POST',headers: { 'Content-Type': 'application/json'},
    body: JSON.stringify({ amount: amount,textarea: textarea,fbOption: fbOption,instOption: instOption,youtubeOption: youtubeOption })})
    .then((data) => {
        
        // console.log("========data",data)
        data.json()
    })
    // console.log("====output======",output)
