const initialState={
    search:{
        city: '',
        niche: ''
    },
    influencers: [],
    allInfluencers: [],
    perPage: 2,
    currPage: 0,
    pageCount: 2,
    influencersProfile: {
        // id: 1,
        // city:"Ranchi",
        // niche: "Food",
        // fbURL:"fb.com",
        // fbLikes:1211,
        // fbFollow: 123,
        // instLike: 1211,
        // instUrl: "@123ds",
        // instFollow: 12326,
        // utubeUrl: "youtu.be",
        // utubeLike: 1232,
        // utubeSubscribers: 2322
    },
    influencersJob: {
        // amount: 12000,
        // textarea: '',
        // isFb: true ,
        // isInsta: false ,
        // isYoutube: true
    },
}

export default (state=initialState,action) => {
    switch(action.type){
        case 'SEARCH_QUERY':
            return{...state, influencers:action.payload}
        case 'INFLUENCER_ALL':{
            console.log("============reducer", state, action);
            let influencers=[...action.payload]
            influencers= influencers.splice(state.currPage*state.perPage, state.perPage )
            return{...state, influencers, allInfluencers:action.payload, pageCount: Math.round(action.payload.length/state.perPage) }
        }
            
        case 'INFLUENCER_PAGINATE':{
            console.log("============allinf", state, action, ...state.allInfluencers);
            let influencers=[...state.allInfluencers]
            influencers = influencers.splice(action.payload.pageNum*state.perPage, state.perPage )
            return{...state, currPage: action.payload.pageNum, influencers: [...influencers], pageCount: Math.round(state.allInfluencers.length/state.perPage)}
        }
        case 'FETCH_PROFILE': {
            return {...state, influencersProfile: action.payload}
        }
            
        case 'INFLUENCER_ALL_ERR':
            return{...state, influencers:[], err: action.payload}
            
        case 'UPDATE_INFLUENCERS_PROFILE':
            return{...state, influencersProfile:action.payload}
        case 'UPDATE_INFLUENCERS_PROFILE_ERR':
            return{...state, influencersProfile: {}, err: action.payload}
        
        case 'SAVE_INFLUENCERS_HIRING': {
            return {...state,  influencersJob: action.payload}
        }
        case 'SAVE_INFLUENCERS_HIRING_ERR':
            return{...state, influencersJob: {}, err: action.payload}
        default:
            return state;
    }
}