import React from 'react';
import './Cards.css';

const Cards =() => {
    return(
        <React.Fragment>
            <h3 className='text-center text-red text-capitalize  '>Our Top Influencer</h3>
            <div class="container-fluid d-flex justify-content-center">
                <div className="row">
                     <div className="col-md-6 col-lg-4 mt-5"><div className="card" >
                        <img className="card-img-top" src="https://femina.wwmindia.com/content/2019/jul/ajeynagar1562839404.jpg" alt="Card image cap" height='200px'/>
                        <div className="card-body">
                        <h5 className="card-title">Roasting-King</h5>
                         <p className="card-text">Ajey Nagar, better known as CarryMinati, is an Indian YouTuber and streamer from Faridabad, India.</p>
                         <a href="#" className="btn btn-primary">View profile</a>
                     </div>
</div></div>
                     <div className="col-md-6 col-lg-4 mt-5"><div className="card" >
                        <img className="card-img-top" src="https://i.pinimg.com/originals/e3/26/47/e3264750bf19ba63c27e1fd32a09930b.jpg" alt="Card image cap" height='200px'/>
                        <div className="card-body">
                        <h5 className="card-title">Tiktoker</h5>
                         <p className="card-text">Jannat Zubair Rahmani is an Indian film and television actress. She also gain popularity from tiktok .</p>
                         <a href="#" className="btn btn-primary">View profile</a>
                     </div>
</div></div>
                     <div className="col-md-6 col-lg-4 mt-5"><div className="card" >
                        <img className="card-img-top" src="https://imgeng.jagran.com/images/2020/nov/Untitled%20design%20(5)1604219641479.jpg" alt="Card image cap" height='200px'/>
                        <div className="card-body">
                        <h5 className="card-title">BB</h5>
                         <p className="card-text">Bhuvan Bam is an Indian comedian, writer, singer, songwriter, and YouTube personality from Delhi, India.</p>
                         <a href="#" className="btn btn-primary">View profile</a>
                     </div>
                     </div></div>
                     
                        
                 </div>
           </div>
        </React.Fragment>
    )
}

export default Cards;