import React,{Component} from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {saveHiredetails} from '../../actions/actionfile';
import {connect} from 'react-redux';


class HireInfluencers extends Component{
    constructor(props){
        super(props);
        this.state ={
            amount: 12000,
            textarea: '',
            isFb: false ,
            isInsta: false ,
            isYoutube: false
            // 
        };
        this.onClickSave = this.onClickSave.bind(this);
        this.handleChange = this.handleChange.bind(this);
    };

    onClickSave(){
        this.props.dispatch(saveHiredetails(this.state.amount,this.state.textarea,this.state.isFb,this.state.isInsta,this.state.isYoutube))
        alert("Details successfully saved")
        // this.props.history.push("/comap")
    }

    handleChange(e) {
        this.setState({  [e.target.name] : e.target.value });
     }

    
   

    render(){
        return(
            <div className="container" style={{marginTop:"5rem"}}>
                <Modal.Dialog >
                <Modal.Header closeButton>
                    <img src = {localStorage.getItem('imurl')} style={{width:"50px",height:"50px"}}/>
                    <Modal.Title>You are hiring {localStorage.getItem('name')}</Modal.Title>
                </Modal.Header>
                
                <Modal.Body >
                <Form>
                    <Form.Group controlId="formBasicDescription">
                        <Form.Label>Amount</Form.Label>
                        <Form.Control type="text" name="amount" placeholder="Enter amount for this task" onChange={this.handleChange} />
                    </Form.Group>
                    <Form.Group controlId="formBasicDescription">
                        <Form.Label>Text Area</Form.Label>
                        <Form.Control type="text" name="textarea" placeholder="Enter your requirement" onChange={this.handleChange} />
                    </Form.Group>
                    {['checkbox'].map((type) => (
                        <div key={`inline-${type}`} className="mb-3">
                        <Form.Check inline label="facebook" type={type} id={`inline-${type}-1`} name="isFb" onChange={this.handleChange}/>
                        <Form.Check inline label="instagram" type={type} id={`inline-${type}-2`} name="isInsta" onChange={this.handleChange}/>
                        <Form.Check
                            inline               
                            label="Youtube"
                            type={type}
                            id={`inline-${type}-3`} name="isYoutube" onChange={this.handleChange}
                        />
                        </div>
                    ))}
                </Form>
                </Modal.Body>

                <Modal.Footer>
                    {/* <Button variant="secondary" onClick={false}>Close</Button> */}
                    <Button variant="primary" onClick = {this.onClickSave}>Hire</Button>
                </Modal.Footer>
                </Modal.Dialog>
            </div>
        )
    }
}

function mapStateToProps(state){
    console.log("==== MAp State========>",state)
    return {influencersJob : state.search.influencersJob}
}

export default connect(mapStateToProps) (HireInfluencers);